import style from './MainDough.module.scss';

function MainDough() {
  const path = process.env.PUBLIC_URL;
  return (
    <>
      <div className={style.dough}>
        <div className={style.doughWrapper}>
          <img src={path + './images/dough-cow.png'} alt="dough-cow" className={style.doughCow}/>
            <img src={path + '/images/dough.png'} alt="   "/>
              <img src={path +'./images/dough-card.svg'} alt="dough-card" className={style.doughCard} />
          </div>

              <div className={style.doughFan}>fantastic</div>
              <div className={style.doughTitle}>CHOCOLATECOOKIE DOUGH</div>
              <p className={style.doughInfo}>            .</p>

              <div className={style.btn}>ORDER NOW</div>

              <div className={style.doughBg}>
              </div>

            </div>


          </>
          );
}

          export default MainDough;