import React, { useEffect, useRef } from 'react';
import style from './MainRepeat.module.scss';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

function MainRepeat() {
  const path = process.env.PUBLIC_URL;

  const repeatRef = useRef(null);
  const iceBgRef = useRef(null);

  useEffect(() => {
    gsap.fromTo(
      iceBgRef.current,
      { rotate: 0, y: 0 },
      {
        rotate: 480,
        y: '250vh',
        scrollTrigger: {
          trigger: repeatRef.current,
          start: ' top 30%',
          end: 'bottom -50%',
          scrub: true,
        },
      }
    );
  }, []); 

  return (
    <div className={style.repeat}>
      <div className={style.smallTitle}>
        <div className={`${style.cloud1} ${style.cloud}`}>
          <img src={path + './images/cloud-01.svg'} alt="cloud1" />
        </div>
        <div className={style.new}>
          <div className={style.newTop}>NEW</div>
          <div className={style.newBottom}>NEW</div>
        </div>
        <div className={`${style.cloud2} ${style.cloud}`}>
          <img src={path + './images/cloud-01.svg'} alt="cloud2" />
        </div>
      </div>

      {/*   */}
      <div ref={iceBgRef} className={style.iceBg}>
        <img src={path + './images/ice-bg.png'} alt="" />
      </div>

      {/*   */}
      <div className={style.iceCream}>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
        <div className={style.iceWrapper}>
          <div className={style.top}>ICECREAM</div>
          <div className={style.bottom}>ICECREAM</div>
        </div>
      </div>

      <div className={style.iceDoughBg}></div>
    </div>
  );
}

export default MainRepeat;
