import style from './MainFollow.module.scss';

function MainFollow(props) {
  const path = process.env.PUBLIC_URL;
  return (
    <>
      <div className={style.follow}>
        <div className={style.followTitle}>
          <div className={style.top}>FOLLOW US</div>
          <div className={style.bottom}>FOLLOW US</div>
        </div>

        <div className={style.followImg}>
          <img src={path + './images/follow-01.png'} alt="follow-img01" />
          <img src={path + './images/follow-02.png'} alt="follow-img02" />
          <img src={path + './images/follow-03.jpg'} alt="follow-img03" />
          <img src={path + './images/follow-04.jpg'} alt="follow-img04" />
          <img src={path + './images/follow-05.png'} alt="follow-img05" />
        </div>
      </div>
    </>
  );
}

export default MainFollow;