import React from 'react';
import style from '../components/Footer.module.scss';

function Footer(props) {
  const path = process.env.PUBLIC_URL;
  return (

    <footer>
      {/* <!-- footer-delivery --> */}
      <div className={style.delivery}>
        <div className={style.deliverytitle}>Delivery Near You</div>
        <div className={style.deliverysearch}>
          <div className={style.font}>Find Instant</div>
          <div className={style.deliverybtn}>
            <img src={path + '../../images/arrow.svg'} alt="arrow" />
            </div>
        </div>
        
      {/* <!-- footer-wave --> */}
      <div className={style.wave}>
        <img src={path + '../../images/wave-01.svg'} alt="wave"/>
      </div>

      </div>

      {/* <!-- footer-logo --> */}
      <div className={style.footerlogo}>
      <img src={path + '../../images/logo.svg'} alt="footerlogo"/>
      </div>

      {/* <!-- footer-info --> */}
      <ul className={style.info}>
        <li><a href="https://github.com/Heeseon1215">GITHUB</a></li>
        <li><a href="https://heeseon1215.github.io/portfolio/">PORTFOLIO</a></li>
      </ul>
    </footer>
  );
}

export default Footer;