import style from './MainFint.module.scss';

function MainFint(props) {
  const path = process.env.PUBLIC_URL;
  return (
    
    <div>
      <div className={style.fint}>
        <div className={style.fintCenter}>
          <p className={style.fintInfo}>   </p>
          <div className={style.fintTitle}>
            <div className={style.top}>FINT</div>
            <div className={style.bottom}>FINT</div>
          </div>
          <div className={style.btn}>VIEW FLAVORS</div>
        </div>
        <div className={style.fintWrapper}>
          <div className={style.fintImg}>
            <img src={path + './images/fint-01.png'} alt="1"/>
            </div>
          <div className={style.fintImg}>
          <img src={path + './images/fint-02.png'} alt="2"/>
            </div>
          <div className={style.fintImg}>
          <img src={path + './images/fint-03.png'} alt="3"/>
            </div>
          <div className={style.fintImg}>
          <img src={path + './images/fint-04.png'} alt="4"/>
            </div>
          <div className={style.fintImg}>
          <img src={path + './images/fint-05.png'} alt="5"/>
            </div>
        </div>
      </div>
    </div>
  );
}

export default MainFint;